<template>
  <div>
    <div class="canvasContainer" ref="canvasContainer"></div>
  </div>
</template>
  
  <script>
import { ref, onMounted } from "vue";
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";

export default {
  name: "RedbullCar",
  setup() {
    const canvasContainer = ref(null);
    let scene, camera, renderer, model;

    const distance = ref(5); // Distance initiale entre la camra et l'objet

    onMounted(() => {
      // Crer une scne
      scene = new THREE.Scene();

      // Crer une camra
      camera = new THREE.PerspectiveCamera(
        75,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      );
      camera.position.z = distance.value;

      // Crer un rendu
      renderer = new THREE.WebGLRenderer({ antialias: true });
      renderer.setSize(window.innerWidth, window.innerHeight);
      canvasContainer.value.appendChild(renderer.domElement);

      // Chargement du modle glTF
      const loader = new GLTFLoader();
      loader.load("driver/scene.gltf", (gltf) => {
        // Obtenir le modle
        model = gltf.scene;

        // Ajouter le modle  la scne
        scene.add(model);
        model.position.y += -2;
        // model.position.x += -1;
        model.position.z += 3;
        // model.rotation.y += 1;
        // model.rotation.z += 0;
        // model.rotation.x += 0.3;

        // Animation de la scne
        const animate = function () {
          requestAnimationFrame(animate);

          // Mise  jour de la position de la camra en fonction de la distance
          camera.position.z = distance.value;

          // Rotation du modle
          //   model.rotation.x += 0.01;
            model.rotation.y += 0.01;

          // Rendu de la scne
          renderer.render(scene, camera);
        };

        // Dfinir la couleur de fond de la scne sur transparent
        scene.background = null;

        // // Activer l'alpha du rendu pour permettre la transparence
        renderer.setClearColor(0x000000, 0); // Remplacez la couleur de fond (0x000000) par la couleur de votre choix
        renderer.setClearAlpha(0);

        // Cration de la lumire ambiante
        const ambientLight = new THREE.AmbientLight(0xffffff, 0.5); // Couleur de la lumire et intensit
        scene.add(ambientLight);

        // Cration de la lumire directionnelle
        const directionalLight = new THREE.DirectionalLight(0xffffff, 1); // Couleur de la lumire et intensit
        directionalLight.position.set(0, 2, 0); // Position de la lumire
        scene.add(directionalLight);

        // Cration de la lumire ponctuelle
        const pointLight = new THREE.PointLight(0xff0000, 1, 10); // Couleur de la lumire, intensit et distance d'atteinte
        pointLight.position.set(0, 5, 0); // Position de la lumire
        scene.add(pointLight);


        // Lancer l'animation
        animate();
        // Rendu de la scne
        renderer.render(scene, camera);
      });
    });

    return {
      canvasContainer,
    };
  },
};
</script>
<style>
.canvasContainer > canvas {
  width: 26em !important;
  height: 20em !important;
}
</style>
  